<!-- 提交成功 -->
<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content"></block>
		</cu-custom>
		<view class="mainsub">
			<view class="padding-xs flex align-center">
				<view class="flex-sub text-center">
					<view class="text-xsl padding">
						<text class=" cuIcon-roundcheckfill text-green"></text>
					</view>
					<view class="textclass">操作成功</view>
					<view class="thanks_titlesmall">
						页面将在 <text class="thanks_titlesmallsub">{{count}}</text> 秒后关闭
					</view>
				</view>

			</view>
		</view>
	</view>
</template>

<script>
	export default {
		onLoad(option) {
			console.log(option)
			this.type = option.type
			this.getCode()
		},
		data() {
			return {
				type:"",
				showsend: true,
				count: '',
				timer: null,

			}
		},
		methods: {
			//倒计时函数
			getCode() {
				const TIME_COUNT = 3;
				if (!this.timer) {
					this.count = TIME_COUNT;
					this.showsend = false;
					this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= TIME_COUNT) {
							this.count--;
						} else {
							this.showsend = true;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000)
				}
			},

		},
		watch: {
			count(val) {
				if (val == 0) {
					//跳转至主页面
					uni.navigateTo({
						url: `../${this.type}/${this.type}`
					});
				}
			},
		}
	}
</script>

<style scoped>
	.text-xsl {
		font-size: 150px;
	}

	.mainsub {
		margin-top: 20%;
	}

	.textclass {
		margin-top: 5px;
		font-weight: 600;
		font-size: 1.2em;
	}
	
	.thanks_titlesmall {
		margin-top: 2%;
		font-size: 1em;
	}
	
	.thanks_titlesmallsub{
		color: #39b54a;
		font-weight: bold;
	}
</style>
